import React from "react";
import {Grid, Paper, Avatar, TextField, Button } from "@material-ui/core";
import ChatIcon from '@material-ui/icons/Chat';
import PowerSettingsNewIcon from '@material-ui/icons/PowerSettingsNew';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';

function Main() {

    const grid1 = {padding:"4.7% 10%"} 
    const paperstyle = {height:"80vh", width:"80vw"}
    const left_grid = {height:"80vh", width: "20vw", align: "left"}
    const right_grid = {height:"80vh", width: "60vw", align: "right"}
    const title = {height: "8vh", width: "20vw", backgroundColor:"#92E3A9"}
    const avatarstyle = {margin:"20% 25%", backroundColor:"#203237"}
    const title_name = {color: "#203237"}
    const buttonstyle = {margin:"10%", backroundColor:"#203237", align:"left"}
    const g1 = {height: "8vh", width: "4vw"}
    const g2 = {height: "8vh", width: "6vw"}
    const g3 = {height: "8vh", width: "10vw"}
    const srh = {backgroundColor: "#92E3A9"}
    const search = {height: "5vh", width: "20vw", margin:"0% 10%"}
    const searchInput = {position: 'relative' }
    const chatTitle = {height: "8vh", width: "60vw", backgroundColor: "#dadada"}
    const chat = {height: "64vh", width: "60vw"}
    const chatType = {height: "8vh", width: "60vw", backgroundColor: "#dadada"}

    return(
        <div style={grid1}>
        <Paper style={paperstyle}>
            <Grid container spacing = {0}>
            <Grid xs={3} style={left_grid}>
            <div style={title}>
                <Grid container spacing = {0}>
                    <Grid xs={2} style={g1}>
                    <Avatar style={avatarstyle}><ChatIcon></ChatIcon></Avatar>
                    </Grid>
                    <Grid xs={3} style={g2}>
                        <h3 style={title_name}>YoCHAT</h3>
                    </Grid>
                    <Grid xs={2}></Grid>
                    <Grid xs={5} style={g3}>
                        <Button Buttontype="submit" endIcon={<PowerSettingsNewIcon/>} color="primary" style={buttonstyle}><strong>Log out</strong></Button>
                    </Grid>
                </Grid>
                
            </div>

            <Divider/>

            <div style={srh}>
                <Grid style={search} container spacing={1} alignItems="flex-end">
                    <Grid item>
                        <SearchIcon />
                    </Grid>
                    <Grid item>
                        <InputBase id="input-with-icon-grid" placeholder="Search....." style={searchInput}/>
                    </Grid>
                </Grid>
                <Divider/>
            </div>

            <div >
            <List component="nav"> 
  <ListItem button>
    <ListItemText primary="Friend 1" />
  </ListItem>
  <Divider />
  <ListItem button divider>
    <ListItemText primary="Friend 2" />
  </ListItem>
  <ListItem button>
    <ListItemText primary="Friend 3" />
  </ListItem>
  <Divider light />
  <ListItem button>
    <ListItemText primary="Friend 4" />
  </ListItem>
</List>
            </div>


                </Grid>
            
            <Divider orientaion="vertical"/>

            <Grid xs={9} style={right_grid}>

                <div style={chatTitle}></div>
                <div style={chat}></div>
                <div style={chatType}></div>



            </Grid>
            </Grid>
        </Paper>
        </div>

    )


} 

export default Main